import {
	getArticles,
	getTags,
	getFeedArticle,
	addFavorite,
	delFavorite,
} from '@/utils/api.js';
import { mapState } from 'vuex';
export default {
	name: 'home',
	async asyncData({ query, store }) {
		const page = Number.parseInt(query.page || 1);
		const limit = 20;
		const tab = query.tab || 'global_feed';
		let fetchArticles =
			store.state.user && tab === 'your_feed'
				? getFeedArticle
				: getArticles;
		let [res, tags] = await Promise.all([
			fetchArticles({
				limit,
				offset: (page - 1) * limit,
				tag: query.tag,
			}),
			getTags(),
		]);
		res.articles.forEach((v) => {
			v.favoriteAbled = false;
		});
		return { ...res, limit, page, ...tags, tag: query.tag, tab };
	},
	watchQuery: ['page', 'tag', 'tab'],
	methods: {
		favorite(item) {
			item.favoriteAbled = true;
			if (item.favorited) {
				delFavorite(item.slug).then(() => {
					item.favorited = false;
					item.favoritesCount -= 1;
					item.favoriteAbled = false;
				});
			} else {
				addFavorite(item.slug).then(() => {
					item.favorited = true;
					item.favoritesCount += 1;
					item.favoriteAbled = false;
				});
			}
		},
	},
	computed: {
		...mapState(['user']),
		totalPage() {
			return Math.ceil(this.articlesCount / this.limit);
		},
	},
};
